  .App {
  	display: -ms-flexbox;
  	display: flex;
  	min-height: 100vh;
  	-ms-flex-direction: column;
  	    flex-direction: column;
  }

  main {
  	-ms-flex: 1 0 auto;
  	    flex: 1 0 auto;
  }
  
  @-webkit-keyframes appears {
  	0%{
  		opacity: 0;
  	}
  	100%{
  		opacity: 1;
  	}
  }
  
  @keyframes appears {
  	0%{
  		opacity: 0;
  	}
  	100%{
  		opacity: 1;
  	}
  }
  .animated {
  	-webkit-animation: appears 2s;
  	        animation: appears 2s
  }



